.App{
    background: linear-gradient(
            106.37deg,
                    #ffe1bc 29.63%,
                    #ffcfd1 51.55%,
                    #f3c6f1 90.85%
            );
    height: 100vh;
    display: flex;
    padding: 1rem;
    /*align-items: center;*/

}
.AppFirstTile{
    width: 350px;
    height: 150px;
    background-color: var(--glass);
    border-radius: 2rem;
    border-color: #fca61f;
    margin-right: 2rem;
    margin-left: 2rem;
    margin-top: 2rem;
    text-align: center;
    font-size: 2rem;

}
.AppSecondTile{
    width: 350px;
