.CompactCard {
    display: flex;
    flex: 1;
    height: 7rem !important;
    width: 10rem;
    border-radius: 0.7rem;
    color: white;
    padding: 1rem;
    position: relative;
    cursor: pointer;
}

.iconDisplay {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    gap: 1rem;
}
.detail>span:nth-child(1) {
    font-size: 33px;
    font-weight: bold;
}